
  /* Global Component */

  #main-container {
    margin-left: 5%;
    margin-right: 5%;
  }

  .buttonPrimary {
    background-color: #813ab1;
    padding: 8px 16px;
    border: 0px;
    color: white;
    border-radius: 5px;
    font-size: 12px;
    text-align: center;
    border:1px solid  #813ab1;
  }
  .buttonPrimary:hover,.buttonSecondary:hover{
    background-color: white;
    color : #813ab1;
    border:1px solid  #813ab1;
  }
  .buttonSecondary{
    background-color: #813ab1;
    text-align: center;
    padding: 12px 16px;
    border: 0px;
    color: white;
    border-radius: 5px;
    font-size: 12px;
    width: 100%;
    font-weight: bolder;
  }
  
  /* Header Component */
  h2 {
    text-align: center;
    font-size: 20px;
    margin: 20px auto;
  }

  /* Balance Component */
  #balance {
    margin-top: 35px;
  }
  #addBalance {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  itemValue {
    font-size: 25px;
  }

  /* AddBalance Component */
  .modalBackground{
    height:100%;
    width:100%;
    position: relative;
  }

  #budgetAdded{
  width: 15%;
  padding-left: 2%;
  padding-top: 2%;
  padding-bottom: 2%;
  border: 1px solid #d1c6c4;
  box-shadow: 0 1px 1px 0 rgba(0, 0, 0, 0.2);
  color:black;
  border-radius: 5px;
  }

  #budgetAdded:focus {
    outline: none !important;
    border:1px solid #813ab1;
  }

  #confirmButton{
    background-color: #556b2f;
    padding: 1% 4%;
    border: 0px;
    color: white;
    border-radius: 5px;
    font-size: 12px;
    text-align: center;
    border:1px solid  #556b2f;
  }
  #cancelButton{
    background-color: #861919;
    padding: 1% 4%;
    border: 0px;
    color: white;
    border-radius: 5px;
    font-size: 12px;
    text-align: center;
    border:1px solid  #861919;
    margin-right: 2%;
  }

  /* Details Component */
  .details {
    display: flex;
    justify-content: space-around;
    text-align: center;
    margin-top: 20px;
    border: 1px solid #d1c6c4;
    border-radius: 5px;
    box-shadow: 0 1px 1px 0 rgba(0, 0, 0, 0.2);
    
  }

  .detailsRow {
    padding: 30px 10%;
    font-size: small;
  }

  /* Card */
  .card{
    display: flex;
    justify-content: space-between;
    align-items: center;
    border: 1px solid #d1c6c4;
    border-radius: 5px;
    padding: 20px 30px;
    margin-top: 15px;
    box-shadow: 0 1px 1px 0 rgba(0, 0, 0, 0.2);
  }
  .expense{
    border-right:8px solid #861919;
  }
  .moneyAdd{
    border-right:8px solid #556b2f;
  }
  
  /* NewTransactions */
  
  #addData, #addAmount{
  width: 92%;
  padding-left: 5%;
  padding-top: 3%;
  padding-bottom: 3%;
  border: 1px solid #d1c6c4;
  box-shadow: 0 1px 1px 0 rgba(0, 0, 0, 0.2);
  color:black;
  border-radius: 5px;
  }

  #addData:focus, #addAmount:focus {
    outline: none !important;
    border:1px solid #813ab1;
  }

  .detailHeading{
    margin: 10px;
  }


  @media screen and (min-width: 600px) and (max-width:900px) {
    #main-container{
      margin-left: 25%;
      margin-right: 25%;
    }
  }

    @media screen and (min-width: 901px) and (max-width: 1200px) {
      #main-container{
        margin-left: 30%;
        margin-right: 30%;
      }

  }

  @media screen and (min-width: 1201px) and (max-width:1400px) {
    #main-container{
      margin-left: 35%;
      margin-right: 35%;
    }
  }

    @media screen and (min-width: 1401px) {
      #main-container{
        margin-left: 37%;
        margin-right: 37%;
      }

}

